@import url(https://fonts.googleapis.com/css2?family=Montserrat&display=swap);
.registeredUsers {
  position: relative;
  right: 0;
  display: flex;
  flex-direction: column;
  margin-top: 20px;
}

.registeredUser__box {
  cursor: default;
  flex: 1 1;
  margin: 10px;
  display: flex;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
  padding: 10px 20px;
  background-color: #ddd;
  justify-content: space-between;
}

.registeredUser__flex {
  display: flex;
  flex-direction: column;
}

.registeredUser__subText {
  color: rgb(6, 150, 247);
  letter-spacing: 1px;
  font-family: "Montserrat";
  margin-bottom: 7px;
  text-transform: lowercase;
  font-size: small;
}

.registeredUser__boldText {
  margin-left: 10px;
  margin-top: 7px;
  font-weight: 600;
  letter-spacing: 1px;
  font-family: "Montserrat";
  font-size: 0.8rem;
}

.manage__postalCodes-headers {
  margin: 0 auto;

  display: flex;
  justify-content: space-between;
  align-items: center;
  background: #ddd;
  padding: 10px 20px;
  margin-bottom: 30px;
  position: sticky;
  top: 70px;
}

.postalCode__box-status {
  width: 10px;
  height: 10px;
  border-radius: 50%;
  margin-left: 25px;
}

.postalCode__box-status-text {
  font-size: medium;
  font-weight: 600;
  letter-spacing: 1.2px;
}

.postalCode__modalMain {
  display: none;
  position: fixed;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  background-color: rgba(0, 0, 0, 0.6);
  z-index: 99;
}

.postalCode__closeBtn {
  cursor: pointer;
  font-size: 2rem;
  color: white;
  position: absolute;
  right: 20px;

  top: 20px;
  font-family: sans-serif;
}

.create__postalCode-modal {
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}

.create__postalCode-modal-container {
  position: absolute;
  min-height: 50vh;
  opacity: 1;
  min-width: 50vw;
  background: #ccc;
  display: flex;
  flex-direction: column;
  padding-top: 40px;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
  -webkit-animation: animatetop 0.3s steps(1000, start);
          animation: animatetop 0.3s steps(1000, start);
}

@-webkit-keyframes animatetop {
  from {
    -webkit-transform: translateY(-70px);
            transform: translateY(-70px);
    opacity: 0;
  }

  to {
    -webkit-transform: translateY(0);
            transform: translateY(0);
    opacity: 1;
  }
}

@keyframes animatetop {
  from {
    -webkit-transform: translateY(-70px);
            transform: translateY(-70px);
    opacity: 0;
  }

  to {
    -webkit-transform: translateY(0);
            transform: translateY(0);
    opacity: 1;
  }
}

label {
  margin: 5px 0 0 50px;
  font-weight: 600;
  letter-spacing: 1.2px;
}

.create_postalCode-modal-input {
  padding: 15px 20px;

  margin: 5px 40px;
  font-size: medium;
  font-weight: 600;
  letter-spacing: 1px;
  border: 1px solid #aaa;
  outline: none;
  border-radius: 40px;
}

.create__modal-submit {
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  margin: auto;
  margin-bottom: 30px;
}

.create__modal-submit-button {
  margin: 0 auto;
  background-color: dodgerblue;
  border: none;
  padding: 10px 20px;
  font-size: medium;
  font-weight: 600;
  min-width: 200px;
  color: white;
}

.postalCode__box {
  margin: 10px auto;
  width: 90%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  border: 1px solid #bbb;
  background: #eee;
  padding: 10px 20px;
  border-radius: 45px;
}

.postalCode__box > div {
  transition: all 0.4s linear;
}

.postalCode__box-sec {
  flex: 1 1;
  text-align: center;
  margin: 5px 5px;
  display: flex;
  flex-direction: column;
}

.postalCode__box-status-sec {
  flex: 1 1;
  text-align: center;
  margin: 5px 5px;
  align-items: center;
  display: flex;
  justify-content: center;
}

.postalCode__box-btn-sec {
  margin: 5px 30px;
  padding: 0 10px;
  display: flex;
}

.postalCode__box-btn-sec button {
  padding: 8px 14px;
  background-color: #ddd;
  margin: 5px 0;
  outline: none;
  border: none;
  font-size: medium;
  font-weight: 600;
  cursor: pointer;
  letter-spacing: 1px;
  -webkit-user-select: none;
      -ms-user-select: none;
          user-select: none;
}

.postalCode__box-btn-sec button:first-child {
  border-radius: 20px 0 0 20px;
}

.postalCode__box-btn-sec button:last-child {
  border-radius: 0 20px 20px 0;
  background: rgb(252, 57, 57);
  color: white;
}

.postalCode__box-title {
  font-size: 12px;
  font-weight: 600;
  text-transform: capitalize;
  letter-spacing: 1.2px;
}

.postalCode__box-value {
  font-size: 17px;
  padding-left: 40px;
  font-weight: 700;
  letter-spacing: 1.4px;
}

.postalCode__box-btns {
  flex: 1 1;

  display: flex;
  justify-content: center;
}

.postalCode__box-btns-btn {
  padding: 4px 8px;
  display: flex;
  align-items: center;
}

.editIcon {
  size: 50px;
}

.manage__postalCodes {
  width: 100%;
}

.create__postalCode {
  width: 90%;
  margin: 20px auto;
  display: flex;
  justify-content: flex-end;
}

.create__postalCode-button {
  padding: 10px 14px;
  font-size: 16px;
  font-weight: 600;
  letter-spacing: 1.4px;
  cursor: pointer;
  margin: 0 5px;
}

.dashboard__main {
  width: 100%;
  position: sticky;
  top: 0;
  z-index: 99;
}

/* Header Side Navigation bar */

.headers__side-nav {
  z-index: 1;
  position: fixed;
  width: 300px;
  top: 0;
  left: -100%;
  height: 100vh;
  background: rgb(224, 224, 224);
  transition: 0.4s ease;
  z-index: 2;
}

.headers__side-nav__container {
  margin: 80px 0;
}

.headers__side-nav-upper-section {
  position: relative;
  width: 100%;
  margin-top: 20px;
}

.headers__side-nav-upper-section-btns {
  width: 100%;
  margin: 0 auto;
  position: relative;
  padding: 10px 0;
  border-bottom: 1px solid lightgray;
}

.headers__side-nav-button {
  cursor: pointer;
  width: 45%;
  padding: 10px 30px;
  font-size: medium;
  margin: 0 5px;
  outline: none;
  border: none;
  min-width: 130px;
  background: black;
  letter-spacing: 0.4px;
  color: white;
  transition: background 0.2s linear;
}

.headers__side-nav-button:hover {
  background: dodgerblue;
  color: black;
}

.headers__side-nav-main-section {
  width: 100%;
  position: relative;
  display: flex;
  flex-direction: column;
  margin-top: 15px;
  line-height: 2;
}

.headers__side-nav-text {
  position: relative;
  margin: 0;
  padding: 10px 15px;
  border-bottom: 1px solid lightgray;
  font-size: larger;
  font-weight: 600;
  letter-spacing: 0.6px;
  cursor: pointer;
  color: black;
  transition: background 0.2s linear;
}

.headers__side-nav-text-order {
  font-size: larger;
  font-weight: 700;
}

.headers__side-nav-text:hover {
  background: dodgerblue;
  color: white;
}

